.application__wrapper{
  padding-top: 38px;
  padding-left: 29px;
  padding-bottom: 49px;
}
.application__title {
  font-size: 21px;
  line-height: 26px;
  font-weight: 800;
  text-transform: uppercase;
}

.application__description{
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
  text-transform: uppercase;
}

.application__find {
  color: rgba(255, 255, 255, 1);
  background-color:rgba(129, 179, 210, 1);
  display: block;
  text-decoration: none;
  font-size: 21px;
  line-height: 26px;
  font-weight: 600;
  text-transform: uppercase;
  width: max-content;
  padding: 16px 49px;
}
