.main-nav {
  background-color: rgba(129, 179, 210, 1);
}

.main-nav__list {
  font-style: bold;
  font-size: 18px;
  font-weight: 900;
  line-height: 26px;
  color: rgba(255, 255, 255, 1);
}

.site-list__item {
  display: block;
  text-decoration: none;
  padding-top: 17px;
  padding-left: 108px;
  box-shadow: 0px 1px 0px rgba(1, 1, 1, 0.2), inset 0px 1px 0px rgba(255, 255, 255, 0.2);
}

.main-header__logo {
  display: block;
  text-decoration: none;
}

.site-list__link{
  display: block;
  text-decoration: none;
  color: rgba(255, 255, 255, 1);
  text-decoration-color: none;
  text-transform: uppercase;
}
